// Nurse Dashboard Page with Medical Incident Management
import React, { useState, useCallback, useEffect } from "react";
import {
  Box,
  Paper,
  useTheme,
  alpha,
  Tabs,
  Tab,
  Typography,
  Button,
  Table,
  TableBody,
  TableCell,
  TableContainer,
  TableHead,
  TableRow,
  Dialog,
  DialogTitle,
  DialogContent,
  DialogActions,
  TextField,
  Grid,
  Card,
  CardContent,
  Stepper,
  Step,
  StepLabel,
  Autocomplete,
  FormControl,
  InputLabel,
  Select,
  MenuItem,
  Chip,
  Alert,
  Snackbar,
  IconButton,
  FormControlLabel,
  Switch,
  LinearProgress,
  Badge,
  TablePagination,
  Tooltip,
  Accordion,
  AccordionSummary,
  AccordionDetails,
} from "@mui/material";
import {
  LocalHospital as HospitalIcon,
  Vaccines as VaccineIcon,
  Add as AddIcon,
  Warning as IncidentIcon,
  Search as SearchIcon,
  Save as SaveIcon,
  Close as CloseIcon,
  Info as InfoIcon,
  Notifications as NotificationsIcon,
  FilterList as FilterIcon,
  Refresh as RefreshIcon,
  ExpandMore as ExpandMoreIcon,
  TrendingUp as TrendingUpIcon,
} from "@mui/icons-material";
import Navbar from "../../components/Navbar";
import ApiStatusSummary from "../../components/ApiStatusSummary";
import { useNavigate } from "react-router-dom";
import { NurseDashboardProps, Student, MedicalIncident } from "../../types";
import { ROUTES } from "../../constants";
import { 
  searchStudents, 
  getStudentMedicalInfo, 
  createMedicalIncident,
  getMedicalIncidents,
  notifyParent 
} from "../../services/apiClient";

// Import test functions for API testing
import { runAllMedicalEventTests } from "../../services/medicalEventApiTest";

// Enhanced Medical Incident Management Component with Statistics
const MedicalIncidentManager: React.FC = () => {
  const [activeStep, setActiveStep] = useState(0);
  const [students, setStudents] = useState<Student[]>([]);
  const [selectedStudent, setSelectedStudent] = useState<Student | null>(null);
  const [searchQuery, setSearchQuery] = useState("");
  const [loading, setLoading] = useState(false);
  const [incidents, setIncidents] = useState<MedicalIncident[]>([]);
  const [showNotification, setShowNotification] = useState(false);
  const [notificationMessage, setNotificationMessage] = useState("");
  const [notificationSeverity, setNotificationSeverity] = useState<"success" | "error" | "info" | "warning">("info");
  const [showApiStatus, setShowApiStatus] = useState(false);
    // Enhanced state for filtering and pagination
  const [page, setPage] = useState(0);
  const [rowsPerPage, setRowsPerPage] = useState(10);
  const [filters, setFilters] = useState({
    severity: "",
    incidentType: "",
    status: "",
    dateFrom: "",
    dateTo: "",
  });
  const [showFilters, setShowFilters] = useState(false);
  const [sortBy] = useState("dateTime");
  const [sortOrder] = useState<"asc" | "desc">("desc");
  
  // Incident form state
  const [incidentForm, setIncidentForm] = useState({
    studentId: 0,
    incidentType: "injury" as const,
    description: "",
    symptoms: [] as string[],
    severity: "low" as const,
    location: "",
    treatmentGiven: "",
    medicationsUsed: [] as string[],
    additionalNotes: "",
    parentNotified: false,
  });
  
  // Statistics state
  const [statistics, setStatistics] = useState({
    totalIncidents: 0,
    todayIncidents: 0,
    criticalIncidents: 0,
    resolvedIncidents: 0,
    incidentsByType: {},
    incidentsBySeverity: {},
    weeklyTrend: [],
  });

  const incidentTypes = [
    { value: "injury", label: "Chn thng" },
    { value: "illness", label: "Bnh tt" },
    { value: "emergency", label: "Cp cu" },
    { value: "allergy", label: "D ng" },
    { value: "other", label: "Khc" },
  ];

  const severityLevels = [
    { value: "low", label: "Thp", color: "success" },
    { value: "medium", label: "Trung bnh", color: "warning" },
    { value: "high", label: "Cao", color: "error" },
    { value: "critical", label: "Nghim trng", color: "error" },
  ];

  const commonSymptoms = [
    "St", "au u", "Bun nn", "Chng mt", "au bng", "Ho", "Kh th", 
    "Pht ban", "au khp", "Mt mi", "Mt  thc", "Xut huyt", "Chut rt"
  ];

  const commonMedications = [
    "Paracetamol", "Ibuprofen", "Bng gc", "Thuc st trng", "Nc mui sinh l",
    "Thuc cm mu", "Thuc chng d ng", "Oxy", "Thuc nh mt", "Thuc ho"
  ];

  const steps = ["Tm Hc Sinh", "Thng Tin Y T", "Ghi Nhn S C", "Xc Nhn"];  // Search students by name only
  const handleSearchStudents = async (query: string) => {
    if (query.length < 2) {
      setStudents([]);
      return;
    }
    
    setLoading(true);
    try {
      const response = await searchStudents(query);
      console.log("Search response:", response);
        // X l response ty theo cu trc d liu t backend
      let studentData = [];
      if (response?.data) {
        studentData = Array.isArray(response.data) ? response.data : [response.data];
      } else if (Array.isArray(response)) {
        studentData = response;
      } else {
        studentData = [];
      }
      
      // Lc hc sinh theo tn (ch tm theo tn)
      const filteredStudents = studentData.filter((student: any) => {
        const studentName = (student.name || student.fullName || "").toLowerCase();
        const searchTerm = query.toLowerCase();
        return studentName.includes(searchTerm);
      });
      
      // Chun ha d liu student t database
      const normalizedStudents = filteredStudents.map((student: any) => ({
        ...student,
        fullName: student.name || student.fullName, // S dng name t database
        id: student.id?.toString() || student.student_code, // m bo c id
      }));
      
      setStudents(normalizedStudents);
      
      if (normalizedStudents.length === 0) {
        setNotificationMessage(`Khng tm thy hc sinh no c tn "${query}"`);
        setNotificationSeverity("info");
        setShowNotification(true);
      } else {
        setNotificationMessage(`Tm thy ${normalizedStudents.length} hc sinh c tn "${query}"`);
        setNotificationSeverity("success");
        setShowNotification(true);
      }
    } catch (error: any) {
      console.error("Error searching students:", error);
      setStudents([]);
      setNotificationMessage(`Li tm kim: ${error.message || 'Khng th kt ni n server'}`);
      setNotificationSeverity("error");
      setShowNotification(true);
    } finally {
      setLoading(false);
    }
  };

  // Select student and get medical info
  const handleSelectStudent = async (student: Student) => {
    setSelectedStudent(student);
    setIncidentForm(prev => ({ ...prev, studentId: student.id }));
      try {
      const medicalInfo = await getStudentMedicalInfo(student.id.toString());
      // Handle medical history if needed
      console.log("Student medical info:", medicalInfo);
    } catch (error) {
      console.error("Error getting medical info:", error);
    }
      setActiveStep(1);
  };
  
  // Create medical incident
  const handleCreateIncident = async () => {
    setLoading(true);
    try {
      await createMedicalIncident(incidentForm);
      setNotificationMessage("S c y t  c ghi nhn thnh cng!");
      setNotificationSeverity("success");
      setShowNotification(true);
        // Reset form
      setActiveStep(0);
      setSelectedStudent(null);
      setIncidentForm({
        studentId: 0,
        incidentType: "injury",
        description: "",
        symptoms: [],
        severity: "low",
        location: "",
        treatmentGiven: "",
        medicationsUsed: [],
        additionalNotes: "",
        parentNotified: false,
      });
      
      // Refresh incidents list
      loadIncidents();
    } catch (error) {
      console.error("Error creating incident:", error);
      setNotificationMessage("C li xy ra khi ghi nhn s c!");
      setNotificationSeverity("error");
      setShowNotification(true);
    } finally {
      setLoading(false);
    }  };

  // Load incidents
  const loadIncidents = async () => {
    try {
      const response = await getMedicalIncidents({
        ...filters,
        sortBy,
        sortOrder,
        page: (page + 1).toString(),
        limit: rowsPerPage.toString(),
      });
      
      let incidentData = [];
      if (response?.data) {
        incidentData = Array.isArray(response.data) ? response.data : [response.data];
      } else if (Array.isArray(response)) {
        incidentData = response;
      } else {
        incidentData = [];
      }
      
      setIncidents(incidentData);
      calculateStatistics(incidentData);
    } catch (error: any) {
      console.error("Error loading incidents:", error);
      setIncidents([]);
      if (error.response?.status !== 404) {
        setNotificationMessage(`Li ti danh sch s c: ${error.message || 'Khng th kt ni n server'}`);
        setNotificationSeverity("error");
        setShowNotification(true);
      }
    }
  };

  // Notify parent
  const handleNotifyParent = async (incidentId: string) => {
    try {
      await notifyParent(incidentId, { message: "Thng bo v s c y t ca con em bn" });
      setNotificationMessage(" thng bo cho ph huynh!");
      setNotificationSeverity("success");
      setShowNotification(true);
      loadIncidents();
    } catch (error) {
      console.error("Error notifying parent:", error);
      setNotificationMessage("C li khi thng bo cho ph huynh!");
      setNotificationSeverity("error");
      setShowNotification(true);
    }
  };

  // Filter and sort handlers
  const handleFilterChange = (filterName: string, value: string) => {
    setFilters(prev => ({ ...prev, [filterName]: value }));
    setPage(0); // Reset to first page when filtering
  };

  const handleClearFilters = () => {
    setFilters({
      severity: "",
      incidentType: "",
      status: "",
      dateFrom: "",
      dateTo: "",
    });
    setPage(0);
  };

  // Pagination handlers
  const handleChangePage = (_: unknown, newPage: number) => {
    setPage(newPage);
  };

  const handleChangeRowsPerPage = (event: React.ChangeEvent<HTMLInputElement>) => {
    setRowsPerPage(parseInt(event.target.value, 10));
    setPage(0);
  };

  // Calculate statistics from incidents
  const calculateStatistics = useCallback((incidentList: MedicalIncident[]) => {
    const today = new Date().toDateString();
    
    const stats = {
      totalIncidents: incidentList.length,
      todayIncidents: incidentList.filter(incident => 
        new Date(incident.dateTime).toDateString() === today
      ).length,
      criticalIncidents: incidentList.filter(incident => 
        incident.severity === "critical" || incident.severity === "high"
      ).length,
      resolvedIncidents: incidentList.filter(incident => 
        incident.status === "resolved"
      ).length,
      incidentsByType: incidentList.reduce((acc: any, incident) => {
        acc[incident.incidentType] = (acc[incident.incidentType] || 0) + 1;
        return acc;
      }, {}),
      incidentsBySeverity: incidentList.reduce((acc: any, incident) => {
        acc[incident.severity] = (acc[incident.severity] || 0) + 1;
        return acc;
      }, {}),
      weeklyTrend: [], // Could be calculated based on dates
    };
    
    setStatistics(stats);
  }, []);

  // Statistics Dashboard Component
  const StatisticsDashboard = () => (
    <Grid container spacing={3} sx={{ mb: 3 }}>
      <Grid item xs={12} md={3}>
        <Card sx={{ bgcolor: 'primary.main', color: 'white' }}>
          <CardContent>
            <Box sx={{ display: 'flex', alignItems: 'center', justifyContent: 'space-between' }}>
              <Box>
                <Typography variant="h4" sx={{ fontWeight: 'bold' }}>
                  {statistics.totalIncidents}
                </Typography>
                <Typography variant="body2">Tng s c</Typography>
              </Box>
              <IncidentIcon sx={{ fontSize: 40, opacity: 0.8 }} />
            </Box>
          </CardContent>
        </Card>
      </Grid>
      
      <Grid item xs={12} md={3}>
        <Card sx={{ bgcolor: 'info.main', color: 'white' }}>
          <CardContent>
            <Box sx={{ display: 'flex', alignItems: 'center', justifyContent: 'space-between' }}>
              <Box>
                <Typography variant="h4" sx={{ fontWeight: 'bold' }}>
                  {statistics.todayIncidents}
                </Typography>
                <Typography variant="body2">Hm nay</Typography>
              </Box>
              <TrendingUpIcon sx={{ fontSize: 40, opacity: 0.8 }} />
            </Box>
          </CardContent>
        </Card>
      </Grid>
      
      <Grid item xs={12} md={3}>
        <Card sx={{ bgcolor: 'error.main', color: 'white' }}>
          <CardContent>
            <Box sx={{ display: 'flex', alignItems: 'center', justifyContent: 'space-between' }}>
              <Box>
                <Typography variant="h4" sx={{ fontWeight: 'bold' }}>
                  {statistics.criticalIncidents}
                </Typography>
                <Typography variant="body2">Nghim trng</Typography>
              </Box>
              <HospitalIcon sx={{ fontSize: 40, opacity: 0.8 }} />
            </Box>
          </CardContent>
        </Card>
      </Grid>
      
      <Grid item xs={12} md={3}>
        <Card sx={{ bgcolor: 'success.main', color: 'white' }}>
          <CardContent>
            <Box sx={{ display: 'flex', alignItems: 'center', justifyContent: 'space-between' }}>
              <Box>
                <Typography variant="h4" sx={{ fontWeight: 'bold' }}>
                  {statistics.resolvedIncidents}
                </Typography>
                <Typography variant="body2"> gii quyt</Typography>
              </Box>
              <VaccineIcon sx={{ fontSize: 40, opacity: 0.8 }} />
            </Box>
          </CardContent>
        </Card>
      </Grid>
    </Grid>
  );

  // Filters Panel Component
  const FiltersPanel = () => (
    <Accordion expanded={showFilters} onChange={() => setShowFilters(!showFilters)}>
      <AccordionSummary expandIcon={<ExpandMoreIcon />}>
        <Box sx={{ display: 'flex', alignItems: 'center', gap: 1 }}>
          <FilterIcon />
          <Typography>B lc tm kim</Typography>
        </Box>
      </AccordionSummary>
      <AccordionDetails>
        <Grid container spacing={2}>
          <Grid item xs={12} md={3}>
            <FormControl fullWidth size="small">
              <InputLabel>Loi s c</InputLabel>
              <Select
                value={filters.incidentType}
                onChange={(e) => handleFilterChange('incidentType', e.target.value)}
              >
                <MenuItem value="">Tt c</MenuItem>
                {incidentTypes.map(type => (
                  <MenuItem key={type.value} value={type.value}>
                    {type.label}
                  </MenuItem>
                ))}
              </Select>
            </FormControl>
          </Grid>
          
          <Grid item xs={12} md={3}>
            <FormControl fullWidth size="small">
              <InputLabel>Mc </InputLabel>
              <Select
                value={filters.severity}
                onChange={(e) => handleFilterChange('severity', e.target.value)}
              >
                <MenuItem value="">Tt c</MenuItem>
                {severityLevels.map(level => (
                  <MenuItem key={level.value} value={level.value}>
                    {level.label}
                  </MenuItem>
                ))}
              </Select>
            </FormControl>
          </Grid>
          
          <Grid item xs={12} md={3}>
            <TextField
              fullWidth
              size="small"
              type="date"
              label="T ngy"
              value={filters.dateFrom}
              onChange={(e) => handleFilterChange('dateFrom', e.target.value)}
              InputLabelProps={{ shrink: true }}
            />
          </Grid>
          
          <Grid item xs={12} md={3}>
            <TextField
              fullWidth
              size="small"
              type="date"
              label="n ngy"
              value={filters.dateTo}
              onChange={(e) => handleFilterChange('dateTo', e.target.value)}
              InputLabelProps={{ shrink: true }}
            />
          </Grid>
          
          <Grid item xs={12}>
            <Box sx={{ display: 'flex', gap: 2 }}>
              <Button 
                variant="contained" 
                onClick={loadIncidents}
                startIcon={<SearchIcon />}
              >
                p dng b lc
              </Button>
              <Button 
                variant="outlined" 
                onClick={handleClearFilters}
              >
                Xa b lc
              </Button>
              <Button 
                variant="outlined" 
                onClick={loadIncidents}
                startIcon={<RefreshIcon />}
              >
                Lm mi
              </Button>
            </Box>
          </Grid>
        </Grid>
      </AccordionDetails>
    </Accordion>
  );  useEffect(() => {
    loadIncidents();
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [filters, sortBy, sortOrder, page, rowsPerPage]);

  useEffect(() => {
    const delayedSearch = setTimeout(() => {
      if (searchQuery) {
        handleSearchStudents(searchQuery);
      }
    }, 300);
    
    return () => clearTimeout(delayedSearch);
  }, [searchQuery]);

  const getSeverityColor = (severity: string) => {
    switch (severity) {
      case "low": return "success";
      case "medium": return "warning";
      case "high": return "error";
      case "critical": return "error";
      default: return "info";
    }
  };
    return (
    <Box sx={{ p: 3 }}>
      <Box sx={{ display: "flex", justifyContent: "space-between", alignItems: "center", mb: 3 }}>
        <Typography variant="h5" sx={{ fontWeight: "bold" }}>
          Qun L S C Y T
        </Typography>        <Box sx={{ display: "flex", gap: 2 }}>
          <Button
            variant="outlined"
            onClick={() => {
              console.log("=== Testing Medical Event APIs ===");
              runAllMedicalEventTests();
              setShowApiStatus(true);
            }}
            sx={{ textTransform: "none" }}
            aria-label="Test API connections"
          >
            Test API
          </Button>
          <Button
            variant="contained"
            startIcon={<AddIcon />}
            onClick={() => setActiveStep(0)}
            sx={{ textTransform: "none" }}
          >
            Ghi Nhn S C Mi
          </Button>
        </Box>
      </Box>

      {/* Statistics Dashboard */}
      <StatisticsDashboard />      {/* Filters Panel */}
      <FiltersPanel />

      {/* API Status Summary */}
      <ApiStatusSummary 
        show={showApiStatus} 
        onClose={() => setShowApiStatus(false)} 
      />{/* Create New Incident Dialog */}
      <Dialog 
        open={activeStep >= 0} 
        maxWidth="md" 
        fullWidth
        aria-labelledby="incident-dialog-title"
        aria-describedby="incident-dialog-description"
        disablePortal={false}
        keepMounted={false}
        onClose={() => setActiveStep(-1)}
      ><DialogTitle id="incident-dialog-title">
          <Box sx={{ display: "flex", justifyContent: "space-between", alignItems: "center" }}>
            <Typography variant="h6">Ghi Nhn S C Y T</Typography>
            <IconButton 
              onClick={() => setActiveStep(-1)}
              aria-label="ng dialog"
            >
              <CloseIcon />
            </IconButton>
          </Box>
        </DialogTitle>        
        <DialogContent id="incident-dialog-description">
          <Stepper activeStep={activeStep} alternativeLabel sx={{ mb: 3 }}>
            {steps.map((label) => (
              <Step key={label}>
                <StepLabel>{label}</StepLabel>
              </Step>
            ))}
          </Stepper>

          {/* Step 1: Search Students */}
          {activeStep === 0 && (            <Box>
              <Typography variant="h6" gutterBottom>
                Tm Kim Hc Sinh Theo Tn
              </Typography>
              
              <Alert severity="info" sx={{ mb: 2 }}>
                <Typography variant="body2">
                  <strong>Hng dn:</strong> Nhp tn hc sinh  tm kim. H thng ch tm kim theo tn, khng bao gm m s hay thng tin khc.
                </Typography>
              </Alert>
                <TextField
                fullWidth
                label="Tm kim hc sinh theo tn"
                placeholder="Nhp tn hc sinh (v d: Nguyn Vn A)..."
                value={searchQuery}
                onChange={(e) => setSearchQuery(e.target.value)}
                InputProps={{
                  startAdornment: <SearchIcon sx={{ color: "text.secondary", mr: 1 }} />
                }}
                sx={{ mb: 3 }}
                helperText="Nhp t nht 2 k t  tm kim theo tn hc sinh"
              />
              
              {loading && <LinearProgress sx={{ mb: 2 }} />}
              
              {students.length > 0 && (
                <Box>
                  <Typography variant="subtitle1" sx={{ mb: 2 }}>
                    Tm thy {students.length} hc sinh c tn "{searchQuery}"
                  </Typography>
                  <Grid container spacing={2}>
                    {students.map((student) => (
                      <Grid item xs={12} md={6} key={student.id}>
                        <Card 
                          sx={{ 
                            cursor: "pointer",
                            "&:hover": { 
                              boxShadow: 4,
                              transform: 'translateY(-2px)',
                              transition: 'all 0.2s ease-in-out'
                            }
                          }}
                          onClick={() => handleSelectStudent(student)}
                        >
                          <CardContent>
                            <Typography variant="h6" sx={{ color: 'primary.main' }}>
                              {student.name}
                            </Typography>
                            <Typography color="text.secondary">
                              <strong>M HS:</strong> {student.student_code || 'N/A'}
                            </Typography>
                            <Typography color="text.secondary">
                              <strong>Lp:</strong> {student.class || 'N/A'}
                            </Typography>
                            <Typography color="text.secondary">
                              <strong>Trng:</strong> {student.school || 'N/A'}
                            </Typography>
                            <Typography color="text.secondary">
                              <strong>Nhm mu:</strong> {student.blood_type || 'N/A'}
                            </Typography>
                            <Box sx={{ mt: 1, display: 'flex', justifyContent: 'flex-end' }}>
                              <Button size="small" variant="contained">
                                Chn hc sinh ny
                              </Button>
                            </Box>
                          </CardContent>
                        </Card>
                      </Grid>
                    ))}
                  </Grid>
                </Box>
              )}
              
              {searchQuery.length >= 2 && students.length === 0 && !loading && (
                <Alert severity="warning" sx={{ mt: 2 }}>
                  <Typography variant="body2">
                    Khng tm thy hc sinh no c tn "{searchQuery}". Vui lng th vi tn khc.
                  </Typography>
                </Alert>
              )}
            </Box>
          )}

          {/* Step 2: Student Medical Info */}
          {activeStep === 1 && selectedStudent && (
            <Box>
              <Card sx={{ mb: 3 }}>
                <CardContent>
                  <Typography variant="h6" gutterBottom>
                    Thng Tin Hc Sinh
                  </Typography>
                  <Grid container spacing={2}>
                    <Grid item xs={12} md={6}>
                      <Typography><strong>H tn:</strong> {selectedStudent.name}</Typography>
                    </Grid>
                    <Grid item xs={12} md={6}>
                      <Typography><strong>Lp:</strong> {selectedStudent.class}</Typography>
                    </Grid>                    <Grid item xs={12} md={6}>
                      <Typography><strong>Ngy sinh:</strong> {selectedStudent.dob}</Typography>
                    </Grid>
                    <Grid item xs={12} md={6}>
                      <Typography><strong>Nhm mu:</strong> {selectedStudent.blood_type}</Typography>
                    </Grid>
                    <Grid item xs={12}>
                      <Typography><strong>Ghi ch y t:</strong> {selectedStudent.parent?.emergencyContact || "Khng c"}</Typography>
                    </Grid>
                  </Grid>
                </CardContent>
              </Card>
              
              <Box sx={{ display: "flex", justifyContent: "space-between" }}>
                <Button onClick={() => setActiveStep(0)}>Quay li</Button>
                <Button variant="contained" onClick={() => setActiveStep(2)}>
                  Tip tc
                </Button>
              </Box>
            </Box>
          )}

          {/* Step 3: Incident Form */}
          {activeStep === 2 && (
            <Box>
              <Grid container spacing={3}>
                <Grid item xs={12} md={6}>
                  <FormControl fullWidth>
                    <InputLabel>Loi s c</InputLabel>
                    <Select
                      value={incidentForm.incidentType}
                      onChange={(e) => setIncidentForm(prev => ({ ...prev, incidentType: e.target.value as any }))}
                    >
                      {incidentTypes.map(type => (
                        <MenuItem key={type.value} value={type.value}>
                          {type.label}
                        </MenuItem>
                      ))}
                    </Select>
                  </FormControl>
                </Grid>
                
                <Grid item xs={12} md={6}>
                  <FormControl fullWidth>
                    <InputLabel>Mc  nghim trng</InputLabel>
                    <Select
                      value={incidentForm.severity}
                      onChange={(e) => setIncidentForm(prev => ({ ...prev, severity: e.target.value as any }))}
                    >
                      {severityLevels.map(level => (
                        <MenuItem key={level.value} value={level.value}>
                          {level.label}
                        </MenuItem>
                      ))}
                    </Select>
                  </FormControl>
                </Grid>
                
                <Grid item xs={12}>
                  <TextField
                    fullWidth
                    label="V tr xy ra s c"
                    value={incidentForm.location}
                    onChange={(e) => setIncidentForm(prev => ({ ...prev, location: e.target.value }))}
                    placeholder="V d: Sn chi, lp hc, phng y t..."
                  />
                </Grid>
                
                <Grid item xs={12}>
                  <TextField
                    fullWidth
                    multiline
                    rows={3}
                    label="M t s c"
                    value={incidentForm.description}
                    onChange={(e) => setIncidentForm(prev => ({ ...prev, description: e.target.value }))}
                    placeholder="M t chi tit v s c  xy ra..."
                  />
                </Grid>
                
                <Grid item xs={12}>
                  <Autocomplete
                    multiple
                    options={commonSymptoms}
                    value={incidentForm.symptoms}
                    onChange={(_, newValue) => setIncidentForm(prev => ({ ...prev, symptoms: newValue }))}
                    renderTags={(value, getTagProps) =>
                      value.map((option, index) => (
                        <Chip variant="outlined" label={option} {...getTagProps({ index })} key={`symptom-${index}`} />
                      ))
                    }
                    renderInput={(params) => (
                      <TextField
                        {...params}
                        label="Triu chng"
                        placeholder="Chn hoc nhp triu chng..."
                      />
                    )}
                  />
                </Grid>
                
                <Grid item xs={12}>
                  <TextField
                    fullWidth
                    multiline
                    rows={3}
                    label="iu tr  thc hin"
                    value={incidentForm.treatmentGiven}
                    onChange={(e) => setIncidentForm(prev => ({ ...prev, treatmentGiven: e.target.value }))}
                    placeholder="M t cc bin php iu tr  thc hin..."
                  />
                </Grid>
                
                <Grid item xs={12}>
                  <Autocomplete
                    multiple
                    options={commonMedications}
                    value={incidentForm.medicationsUsed}
                    onChange={(_, newValue) => setIncidentForm(prev => ({ ...prev, medicationsUsed: newValue }))}
                    renderTags={(value, getTagProps) =>
                      value.map((option, index) => (
                        <Chip variant="outlined" label={option} {...getTagProps({ index })} key={`medication-${index}`} />
                      ))
                    }
                    renderInput={(params) => (
                      <TextField
                        {...params}
                        label="Thuc/Vt t y t  s dng"
                        placeholder="Chn hoc nhp thuc  s dng..."
                      />
                    )}
                  />
                </Grid>
                
                <Grid item xs={12}>
                  <TextField
                    fullWidth
                    multiline
                    rows={2}
                    label="Ghi ch thm"
                    value={incidentForm.additionalNotes}
                    onChange={(e) => setIncidentForm(prev => ({ ...prev, additionalNotes: e.target.value }))}
                    placeholder="Ghi ch thm (nu c)..."
                  />
                </Grid>
                
                <Grid item xs={12}>
                  <FormControlLabel
                    control={
                      <Switch
                        checked={incidentForm.parentNotified}
                        onChange={(e) => setIncidentForm(prev => ({ ...prev, parentNotified: e.target.checked }))}
                      />
                    }
                    label=" thng bo cho ph huynh"
                  />
                </Grid>
              </Grid>
              
              <Box sx={{ display: "flex", justifyContent: "space-between", mt: 3 }}>
                <Button onClick={() => setActiveStep(1)}>Quay li</Button>
                <Button variant="contained" onClick={() => setActiveStep(3)}>
                  Xem trc
                </Button>
              </Box>
            </Box>
          )}

          {/* Step 4: Confirmation */}
          {activeStep === 3 && selectedStudent && (
            <Box>
              <Typography variant="h6" gutterBottom>
                Xc Nhn Thng Tin
              </Typography>
              
              <Card sx={{ mb: 3 }}>
                <CardContent>
                  <Grid container spacing={2}>
                    <Grid item xs={12} md={6}>
                      <Typography variant="subtitle2" color="text.secondary">Hc sinh</Typography>
                      <Typography variant="body1">{selectedStudent.name} - {selectedStudent.class}</Typography>
                    </Grid>
                    <Grid item xs={12} md={6}>
                      <Typography variant="subtitle2" color="text.secondary">Loi s c</Typography>
                      <Typography variant="body1">
                        {incidentTypes.find(t => t.value === incidentForm.incidentType)?.label}
                      </Typography>
                    </Grid>
                    <Grid item xs={12} md={6}>
                      <Typography variant="subtitle2" color="text.secondary">Mc </Typography>
                      <Chip 
                        label={severityLevels.find(s => s.value === incidentForm.severity)?.label}
                        color={getSeverityColor(incidentForm.severity) as any}
                        size="small"
                      />
                    </Grid>
                    <Grid item xs={12} md={6}>
                      <Typography variant="subtitle2" color="text.secondary">V tr</Typography>
                      <Typography variant="body1">{incidentForm.location}</Typography>
                    </Grid>
                    <Grid item xs={12}>
                      <Typography variant="subtitle2" color="text.secondary">M t</Typography>
                      <Typography variant="body1">{incidentForm.description}</Typography>
                    </Grid>
                    {incidentForm.symptoms.length > 0 && (
                      <Grid item xs={12}>
                        <Typography variant="subtitle2" color="text.secondary">Triu chng</Typography>
                        <Box sx={{ display: "flex", flexWrap: "wrap", gap: 1, mt: 1 }}>
                          {incidentForm.symptoms.map((symptom, index) => (
                            <Chip key={index} label={symptom} size="small" />
                          ))}
                        </Box>
                      </Grid>
                    )}
                    <Grid item xs={12}>
                      <Typography variant="subtitle2" color="text.secondary">iu tr</Typography>
                      <Typography variant="body1">{incidentForm.treatmentGiven}</Typography>
                    </Grid>
                    {incidentForm.medicationsUsed.length > 0 && (
                      <Grid item xs={12}>
                        <Typography variant="subtitle2" color="text.secondary">Thuc/Vt t s dng</Typography>
                        <Box sx={{ display: "flex", flexWrap: "wrap", gap: 1, mt: 1 }}>
                          {incidentForm.medicationsUsed.map((med, index) => (
                            <Chip key={index} label={med} size="small" />
                          ))}
                        </Box>
                      </Grid>
                    )}
                  </Grid>
                </CardContent>
              </Card>
              
              <Box sx={{ display: "flex", justifyContent: "space-between" }}>
                <Button onClick={() => setActiveStep(2)}>Quay li</Button>
                <Button 
                  variant="contained" 
                  onClick={handleCreateIncident}
                  disabled={loading}
                  startIcon={loading ? <LinearProgress /> : <SaveIcon />}
                >
                  {loading ? "ang lu..." : "Lu s c"}
                </Button>
              </Box>
            </Box>
          )}
        </DialogContent>
      </Dialog>

      {/* Enhanced Incidents List */}
      <Paper sx={{ mt: 3 }}>
        <Box sx={{ p: 2, borderBottom: 1, borderColor: "divider" }}>
          <Typography variant="h6">Danh Sch S C Y T</Typography>
        </Box>
        
        <TableContainer>
          <Table>
            <TableHead>
              <TableRow>
                <TableCell>Hc sinh</TableCell>
                <TableCell>Loi s c</TableCell>
                <TableCell>Mc </TableCell>
                <TableCell>Thi gian</TableCell>
                <TableCell>Trng thi</TableCell>
                <TableCell>Thao tc</TableCell>
              </TableRow>
            </TableHead>
            <TableBody>
              {incidents
                .slice(page * rowsPerPage, page * rowsPerPage + rowsPerPage)
                .map((incident) => (
                <TableRow key={incident.id}>
                  <TableCell>
                    <Box>
                      <Typography variant="body2" fontWeight="medium">
                        {incident.studentName}
                      </Typography>
                      <Typography variant="caption" color="text.secondary">
                        {incident.className}
                      </Typography>
                    </Box>
                  </TableCell>
                  <TableCell>
                    {incidentTypes.find(t => t.value === incident.incidentType)?.label}
                  </TableCell>
                  <TableCell>
                    <Chip 
                      label={severityLevels.find(s => s.value === incident.severity)?.label}
                      color={getSeverityColor(incident.severity) as any}
                      size="small"
                    />
                  </TableCell>
                  <TableCell>
                    <Typography variant="body2">
                      {new Date(incident.dateTime).toLocaleDateString('vi-VN')}
                    </Typography>
                    <Typography variant="caption" color="text.secondary">
                      {new Date(incident.dateTime).toLocaleTimeString('vi-VN')}
                    </Typography>
                  </TableCell>
                  <TableCell>
                    <Chip 
                      label={incident.status === 'active' ? 'ang x l' : 
                             incident.status === 'resolved' ? ' gii quyt' : 'Cn theo di'}
                      color={incident.status === 'resolved' ? 'success' : 'warning'}
                      size="small"
                    />
                  </TableCell>
                  <TableCell>
                    <Box sx={{ display: "flex", gap: 1 }}>
                      <Tooltip title={incident.parentNotified ? " thng bo ph huynh" : "Cha thng bo ph huynh"}>
                        <IconButton 
                          size="small" 
                          onClick={() => handleNotifyParent(incident.id)}
                          disabled={incident.parentNotified}
                        >
                          <Badge 
                            color={incident.parentNotified ? "success" : "error"}
                            variant="dot"
                          >
                            <NotificationsIcon />
                          </Badge>
                        </IconButton>
                      </Tooltip>
                      <Tooltip title="Xem chi tit">
                        <IconButton size="small">
                          <InfoIcon />
                        </IconButton>
                      </Tooltip>
                    </Box>
                  </TableCell>
                </TableRow>
              ))}
            </TableBody>
          </Table>
          
          <TablePagination
            rowsPerPageOptions={[5, 10, 25]}
            component="div"
            count={incidents.length}
            rowsPerPage={rowsPerPage}
            page={page}
            onPageChange={handleChangePage}
            onRowsPerPageChange={handleChangeRowsPerPage}
            labelRowsPerPage="S dng mi trang:"
            labelDisplayedRows={({ from, to, count }) => `${from}-${to} trong ${count !== -1 ? count : `hn ${to}`}`}
          />
        </TableContainer>
      </Paper>

      {/* Notification Snackbar */}
      <Snackbar 
        open={showNotification} 
        autoHideDuration={6000}
        onClose={() => setShowNotification(false)}
        anchorOrigin={{ vertical: 'top', horizontal: 'right' }}
      >
        <Alert 
          onClose={() => setShowNotification(false)} 
          severity={notificationSeverity}
          sx={{ width: '100%' }}
        >
          {notificationMessage}
        </Alert>
      </Snackbar>
    </Box>
  );
};

const NurseDashboard: React.FC<NurseDashboardProps> = ({ onLogout }) => {
  const theme = useTheme();
  const navigate = useNavigate();
  const [tabValue, setTabValue] = useState(0);
  const [openDialog, setOpenDialog] = useState(false);
  const [newSupply, setNewSupply] = useState({
    name: "",
    quantity: "",
    expiryDate: "",
    location: "",
  });
  
  const handleNavigateToHome = useCallback(() => {
    navigate(ROUTES.HOME);
  }, [navigate]);

  const handleNavigateToNews = useCallback(() => {
    navigate(ROUTES.HOME);
    setTimeout(() => {
      const el = document.getElementById("school-health-news");
      if (el) el.scrollIntoView({ behavior: "smooth" });
    }, 100);
  }, [navigate]);

  const handleNavigateToContact = useCallback(() => {
    navigate(ROUTES.HOME);
    setTimeout(() => {
      const el = document.getElementById("contact");
      if (el) el.scrollIntoView({ behavior: "smooth" });
    }, 100);
  }, [navigate]);

  const handleTabChange = (_event: React.SyntheticEvent, newValue: number) => {
    setTabValue(newValue);
  };

  const handleDialogOpen = () => {
    setOpenDialog(true);
  };

  const handleDialogClose = () => {
    setOpenDialog(false);
  };

  const handleInputChange = (event: React.ChangeEvent<HTMLInputElement>) => {
    const { name, value } = event.target;
    setNewSupply((prev) => ({ ...prev, [name]: value }));
  };

  const handleAddSupply = () => {
    setOpenDialog(false);
  };

  return (
    <>
      <Navbar
        onLogout={onLogout}
        onNavigateToHome={handleNavigateToHome}
        onNavigateToNews={handleNavigateToNews}
        onNavigateToContact={handleNavigateToContact}
      />
      <Box
        sx={{
          width: "100%",
          p: 3,
          backgroundColor: alpha(theme.palette.background.default, 0.8),
        }}
      >
        <Box
          sx={{
            mb: 4,
            display: "flex",
            alignItems: "center",
            gap: 2,
            justifyContent: "space-between",
          }}
        >
          <Box sx={{ display: "flex", alignItems: "center", gap: 2 }}>
            <HospitalIcon
              sx={{ fontSize: 40, color: theme.palette.primary.main }}
            />
            <Typography
              variant="h4"
              sx={{ fontWeight: "bold", color: theme.palette.primary.main }}
            >
              Qun L Y T Hc ng
            </Typography>
          </Box>
        </Box>

        <Tabs
          value={tabValue}
          onChange={handleTabChange}
          sx={{
            mb: 3,
            "& .MuiTab-root": {
              textTransform: "none",
              fontWeight: "medium",
              minHeight: 48,
            },
          }}
        >
          <Tab
            icon={<HospitalIcon />}
            label="Vt T Y T"
            iconPosition="start"
          />
          <Tab
            icon={<VaccineIcon />}
            label="Lch Tim Chng"
            iconPosition="start"
          />
          <Tab
            icon={<AddIcon />}
            label="n Yu Cu Thuc"
            iconPosition="start"
          />
          <Tab
            icon={<IncidentIcon />}
            label="S C Y T"
            iconPosition="start"
          />
        </Tabs>

        {tabValue === 0 && (
          <Box sx={{ mb: 4 }}>
            <Paper
              sx={{
                p: 3,
                display: "flex",
                alignItems: "center",
                justifyContent: "space-between",
              }}
            >
              <Box>
                <Typography variant="h6">Tng Quan Vt T Y T</Typography>
                <Typography variant="h4" color="primary">
                  0
                </Typography>
                <Typography variant="body2" color="text.secondary">
                  Tng s vt t y t trong kho
                </Typography>
              </Box>
              <Button
                variant="contained"
                startIcon={<AddIcon />}
                sx={{ textTransform: "none" }}
                onClick={handleDialogOpen}
              >
                Thm Vt T Mi
              </Button>
            </Paper>
          </Box>
        )}

        {tabValue === 0 && (
          <TableContainer component={Paper}>
            <Table>
              <TableHead>
                <TableRow>
                  <TableCell>Tn Vt T</TableCell>
                  <TableCell>S Lng</TableCell>
                  <TableCell>Hn S Dng</TableCell>
                  <TableCell>Thao Tc</TableCell>
                </TableRow>
              </TableHead>
              <TableBody>
                <TableRow>
                  <TableCell>Bng Gc</TableCell>
                  <TableCell>50</TableCell>
                  <TableCell>2025-12-31</TableCell>
                  <TableCell>
                    <Button variant="contained" color="primary">
                      Sa
                    </Button>
                  </TableCell>
                </TableRow>
                <TableRow>
                  <TableCell>Thuc St Trng</TableCell>
                  <TableCell>30</TableCell>
                  <TableCell>2025-11-15</TableCell>
                  <TableCell>
                    <Button variant="contained" color="primary">
                      Sa
                    </Button>
                  </TableCell>
                </TableRow>
                <TableRow>
                  <TableCell>Khu Trang</TableCell>
                  <TableCell>100</TableCell>
                  <TableCell>2026-01-01</TableCell>
                  <TableCell>
                    <Button variant="contained" color="primary">
                      Sa
                    </Button>
                  </TableCell>
                </TableRow>
              </TableBody>
            </Table>
          </TableContainer>
        )}

        {tabValue === 1 && (
          <Box>
            <Typography variant="h5" sx={{ mb: 3 }}>
              Lch Tim Chng
            </Typography>
            <TableContainer component={Paper}>
              <Table>
                <TableHead>
                  <TableRow>
                    <TableCell>Tn Hc Sinh</TableCell>
                    <TableCell>Loi Vc Xin</TableCell>
                    <TableCell>Tng S Mi</TableCell>
                    <TableCell> Tim</TableCell>
                    <TableCell>Ngy Tim Tip Theo</TableCell>
                    <TableCell>Trng Thi</TableCell>
                  </TableRow>
                </TableHead>
                <TableBody>
                  <TableRow>
                    <TableCell>Nguyn Vn A</TableCell>
                    <TableCell>Hepatitis B</TableCell>
                    <TableCell>3</TableCell>
                    <TableCell>2</TableCell>
                    <TableCell>2024-04-15</TableCell>
                    <TableCell sx={{ color: "orange" }}>ang Ch</TableCell>
                  </TableRow>
                  <TableRow>
                    <TableCell>Trn Th B</TableCell>
                    <TableCell>MMR</TableCell>
                    <TableCell>2</TableCell>
                    <TableCell>1</TableCell>
                    <TableCell>2024-03-20</TableCell>
                    <TableCell sx={{ color: "orange" }}>ang Ch</TableCell>
                  </TableRow>
                  <TableRow>
                    <TableCell>L Vn C</TableCell>
                    <TableCell>DTaP</TableCell>
                    <TableCell>5</TableCell>
                    <TableCell>5</TableCell>
                    <TableCell>2024-05-01</TableCell>
                    <TableCell sx={{ color: "green" }}>Hon Thnh</TableCell>
                  </TableRow>
                </TableBody>
              </Table>
            </TableContainer>
          </Box>
        )}

        {tabValue === 2 && (
          <Box>
            <Typography variant="h5" sx={{ mb: 3 }}>
              n Yu Cu S Dng Thuc
            </Typography>
            <TableContainer component={Paper}>
              <Table>
                <TableHead>
                  <TableRow>
                    <TableCell>Tn Hc Sinh</TableCell>
                    <TableCell>Tn Ph Huynh</TableCell>
                    <TableCell>Tn Thuc</TableCell>
                    <TableCell>Liu Dng</TableCell>
                    <TableCell>Tn Sut</TableCell>
                    <TableCell>Thi Gian</TableCell>
                    <TableCell>L Do</TableCell>
                    <TableCell>Trng Thi</TableCell>
                    <TableCell>Ghi Ch</TableCell>
                  </TableRow>
                </TableHead>
                <TableBody>
                  <TableRow>
                    <TableCell>Nguyn Vn A</TableCell>
                    <TableCell>Nguyn Vn B</TableCell>
                    <TableCell>Paracetamol</TableCell>
                    <TableCell>500mg</TableCell>
                    <TableCell>3 ln/ngy</TableCell>
                    <TableCell>2024-03-15 - 2024-03-20</TableCell>
                    <TableCell>St cao</TableCell>
                    <TableCell sx={{ color: "orange" }}>ang Ch</TableCell>
                    <TableCell>Ung sau khi n</TableCell>
                  </TableRow>
                  <TableRow>
                    <TableCell>Trn Th B</TableCell>
                    <TableCell>Trn Th C</TableCell>
                    <TableCell>Amoxicillin</TableCell>
                    <TableCell>250mg</TableCell>
                    <TableCell>2 ln/ngy</TableCell>
                    <TableCell>2024-03-14 - 2024-03-21</TableCell>
                    <TableCell>Vim hng</TableCell>
                    <TableCell sx={{ color: "green" }}> Duyt</TableCell>
                    <TableCell>Ung trc ba n 30 pht</TableCell>
                  </TableRow>
                </TableBody>
              </Table>
            </TableContainer>
          </Box>
        )}

        {tabValue === 3 && <MedicalIncidentManager />}

        <Dialog
          open={openDialog}
          onClose={(_event, reason) => {
            if (reason !== "backdropClick") {
              handleDialogClose();
            }
          }}
          disableEscapeKeyDown
        >
          <DialogTitle>Thm Vt T Y T</DialogTitle>
          <DialogContent>
            <TextField
              margin="dense"
              label="Tn Vt T"
              name="name"
              fullWidth
              value={newSupply.name}
              onChange={handleInputChange}
            />
            <TextField
              margin="dense"
              label="S Lng"
              name="quantity"
              fullWidth
              value={newSupply.quantity}
              onChange={handleInputChange}
            />
            <TextField
              margin="dense"
              label="Hn S Dng"
              name="expiryDate"
              fullWidth
              value={newSupply.expiryDate}
              onChange={handleInputChange}
            />
            <TextField
              margin="dense"
              label="V Tr"
              name="location"
              fullWidth
              value={newSupply.location}
              onChange={handleInputChange}
            />
          </DialogContent>
          <DialogActions>
            <Button onClick={handleDialogClose}>Hy</Button>
            <Button onClick={handleAddSupply} variant="contained">
              Thm
            </Button>
          </DialogActions>
        </Dialog>
      </Box>
    </>
  );
};

export default NurseDashboard;
