import React from "react";
import Navbar from "../../components/Navbar";
import { useNavigate } from "react-router-dom";

const VaccinationNews: React.FC<{ onLogout?: () => void }> = ({ onLogout }) => {
  const navigate = useNavigate();

  const handleNavigateToHome = () => {
    navigate("/");
  };
  const handleNavigateToNews = () => {
    navigate("/");
    setTimeout(() => {
      const el = document.getElementById("school-health-news");
      if (el) el.scrollIntoView({ behavior: "smooth" });
    }, 100);
  };
  const handleNavigateToContact = () => {
    navigate("/");
    setTimeout(() => {
      const el = document.getElementById("contact");
      if (el) el.scrollIntoView({ behavior: "smooth" });
    }, 100);
  };

  const vaccineNews = [
    {
      vaccineName: "COVID-19",
      description: "Vaccin COVID-19 gip gim nguy c mc bnh v bin chng nghim trng.",
      benefits: "Bo v sc khe cng ng, gim nguy c ly nhim.",
    },
    {
      vaccineName: "Vim gan B",
      description: "Vaccin Vim gan B gip ngn nga bnh vim gan B v cc bin chng lin quan.",
      benefits: "Bo v gan, gim nguy c ung th gan.",
    },
    {
      vaccineName: "Si",
      description: "Vaccin Si gip ngn nga bnh si v cc bin chng nghim trng.",
      benefits: "Bo v tr em khi bnh si, gim nguy c t vong.",
    },
    {
      vaccineName: "Cm",
      description: "Vaccin Cm gip gim nguy c mc bnh cm v bin chng nghim trng.",
      benefits: "Bo v sc khe trong ma cm, gim nguy c ly lan.",
    },
    {
      vaccineName: "Thy u",
      description: "Vaccin Thy u gip ngn nga bnh thy u v cc bin chng nghim trng.",
      benefits: "Bo v tr em khi bnh thy u, gim nguy c bin chng.",
    },
  ];

  return (
    <>
      <Navbar {...(onLogout ? { onLogout } : {})}
        onNavigateToHome={handleNavigateToHome}
        onNavigateToNews={handleNavigateToNews}
        onNavigateToContact={handleNavigateToContact}
      />
      <div
        style={{
          padding: "20px",
          backgroundColor: "#f4f4f4",
          minHeight: "100vh",
        }}
      >
        <h1 style={{ textAlign: "center", marginBottom: "20px" }}>Tin tc v vaccin</h1>
        <p style={{ textAlign: "center", marginBottom: "20px", fontSize: "16px", color: "#555" }}>
          Hin ti trng ang trin khai cc chng trnh tim chng vaccin nhm bo v sc khe hc sinh v cng ng.
          Di y l thng tin chi tit v cc loi vaccin v li ch ca chng.
        </p>
        <p style={{ textAlign: "center", marginBottom: "20px", fontSize: "16px", color: "#555" }}>
          Hon cnh bnh hin ti: Mt s bnh nh COVID-19, Vim gan B, Si, Cm, v Thy u vn ang l mi nguy c ln i vi sc khe cng ng.
          Vic tim chng l bin php hiu qu  phng nga v gim thiu tc ng ca cc bnh ny.
        </p>
        {vaccineNews.map((news, index) => (
          <div
            key={index}
            style={{
              backgroundColor: "white",
              padding: "20px",
              marginBottom: "20px",
              borderRadius: "10px",
              boxShadow: "0 4px 8px rgba(0, 0, 0, 0.2)",
            }}
          >
            <h2 style={{ color: "#3498db" }}>{news.vaccineName}</h2>
            <p><strong>Gii thiu:</strong> {news.description}</p>
            <p><strong>Li ch:</strong> {news.benefits}</p>
          </div>
        ))}
        <button
          style={{
            backgroundColor: "#2ecc71",
            color: "white",
            padding: "10px 24px",
            border: "none",
            borderRadius: "6px",
            fontSize: "16px",
            cursor: "pointer",
            display: "block",
            margin: "32px auto",
          }}
          onClick={() => navigate("/parent/vaccination-event-dashboard")}
        >
          Quay v trang tim chng
        </button>
      </div>
    </>
  );
};

export default VaccinationNews;
